// ** Third Party Components
import classnames from 'classnames'
import { TrendingUp } from 'react-feather'

// ** Custom Components
import Avatar from '@components/avatar'
import axios from '@src/configs/axios/axiosConfig'
// ** Reactstrap Imports
import { Card, CardHeader, CardTitle, CardBody, CardText, Row, Col } from 'reactstrap'
import { useEffect, useState } from 'react'

const data = [
  {
    status: 1,
    title: '0',
    subtitle: 'Assigned',
    color: 'light-primary',
    icon: <TrendingUp size={24} />
  },
  {
    status: 2,
    title: '0',
    subtitle: 'Inprogress',
    color: 'light-primary',
    icon: <TrendingUp size={24} />
  },
  {
    status: 3,
    title: '0',
    subtitle: 'Completed',
    color: 'light-primary',
    icon: <TrendingUp size={24} />
  },
  {
    status: 6,
    title: '0',
    subtitle: 'Pending Review',
    color: 'light-primary',
    icon: <TrendingUp size={24} />
  },
  {
    status: 7,
    title: '0',
    subtitle: 'Request Changes',
    color: 'light-primary',
    icon: <TrendingUp size={24} />
  }
]

const StatsCard = ({ cols }) => {

  const [dataArr, setData] = useState([])

  const getStats = () => {
    axios.post('tasks/taskcountbystatus')
      .then((res) => {
        data.forEach((obj, key) => {
          const ind = res.data.tasks.findIndex((o) => parseInt(o.taskstatus) === obj.status)
          if (ind > -1) {
            data[key].title = res.data.tasks[ind].count
          }
        })
        setData(data)
      })
      .catch((err) => {
        console.log(err)
      })
  }

  useEffect(() => {
    getStats()
  }, [])

  const renderData = () => {
    return dataArr.map((item, index) => {
      const colMargin = Object.keys(cols)
      const margin = index === 5 ? 'sm' : colMargin[0]
      return (
        <Col
          key={index}
          {...cols}
          className={classnames({
            [`mb-2 mb-${margin}-0`]: index !== data.length - 1
          })}
        >
          <div className='d-flex align-items-center'>
            <Avatar color={item.color} icon={item.icon} className='me-2' />
            <div className='my-auto'>
              <h6 className='fw-bolder mb-0'>{item.title}</h6>
              <CardText className='font-small-3 mb-0'>{item.subtitle}</CardText>
            </div>
          </div>
        </Col>
      )
    })
  }

  return (
    <Card className='card-statistics'>
      <CardHeader>
        <CardTitle tag='h4'>Statistics</CardTitle>
        {/* <CardText className='card-text font-small-2 me-25 mb-0'>Updated 1 month ago</CardText> */}
      </CardHeader>
      <CardBody className='statistics-body'>
        <Row>{renderData()}</Row>
      </CardBody>
    </Card>
  )
}

export default StatsCard
