// ** Custom Components
import Avatar from '@components/avatar'

// ** Icons Imports
import { Check } from 'react-feather'
import * as Icon from 'react-feather'
// ** Reactstrap Imports
import { Card, CardHeader, CardTitle, CardBody } from 'reactstrap'
import { useEffect, useState } from 'react'
import axios from '@src/configs/axios/axiosConfig'
import { orgUserId } from '@src/helper/sassHelper'
import moment from 'moment'

const userId = orgUserId()

const MyOverdueTasks = () => {

  const dateFormat = (value) => {

    return moment.unix(value).format("MMM DD, YYYY")

  }

  const endDate  = moment().startOf('day').unix()

  const [data, setData] = useState([])
  const getTasks = () => {
    axios.post('/tasks/dashboardmytaskoverdues', { userId, endDate })
      .then((res) => {
        setData(res.data.tasks)
      })
      .catch((err) => { console.log(err) })
  }

  useEffect(() => {
    getTasks()
  }, [])

  const renderTransactions = () => {
    return data.map(item => {
      return (
        <div key={item.servicename} className='transaction-item'>
          <div className='d-flex'>
            <Avatar className='rounded' size={12} color={'priamry'} icon={<Check size={12} />} />
            <div>
              <h6 className='transaction-title'>{item.servicename}</h6>
              <small>{item.clientname}</small>
            </div>
          </div>
          <div className={`fw-bolder text-danger`}>{dateFormat(item.enddate)}</div>
        </div>
      )
    })
  }

  return (
    <Card className='card-transaction'>
      <CardHeader>
        <CardTitle tag='h4'>
          <Icon.MoreVertical size={18} />
          My Tasks</CardTitle>
      </CardHeader>
      <CardBody>{renderTransactions()}</CardBody>
    </Card>
  )
}

export default MyOverdueTasks
